.header-admin {
  height: 100vh;
  background-image: 
  linear-gradient(to right bottom, 
   rgba(80, 80, 80, 0.8),
   rgba(60, 63, 66, 0.8)),
   url('https://wallpaperboat.com/wp-content/uploads/2019/05/lion-art-011.jpg');
  
  background-size: cover;
  background-position: top;
  position: relative;
  padding: 500px;
}

.brand-box {
  position: absolute;
  top: 40px;
  left: 40px;
}

.brand { 
  font-size: 20px;
  color: #fff;
}

.text-box {
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.heading-primary {
  color: #fff;
  text-transform: uppercase;

  backface-visibility: hidden;
  margin-bottom: 30px;
}

.heading-primary-main {
  display: block;
  font-size: 26px;
  font-weight: 400;
  letter-spacing: 5px;
}

.heading-primary-sub {
  display: block;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 7.4px;
}

.btn:link,
.btn:visited {
  text-transform: uppercase;
  text-decoration: none;
  padding: 10px 20px;
  display: inline-block;
  border-radius: 100px;
  transition: all .2s;
  position: relative;
}

.btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.btn:active {
  transform: translateY(-1px);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}

.btn-white {
  background-color: #fff;
  color: #777;
  font-size: 14px;
}

.input-admin, select {
  width: 60%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

table {
  border-collapse: collapse;
}

table, th, td {
  text-align: center;
  border: 1px solid black;
}
th {
  width: 100px;
  height: 50px;
}